.BasicInfoDiv{

    /* for app js grid */
    grid-row: span 2;

    padding: 20px;
    width: 100%;
    /* max-width: 300px; */

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;

    grid-gap: 20px;

    gap: 20px;
}

@media only screen and (max-width: 720px) {
    .BasicInfoDiv{
        grid-column: span 2;
    }
    .BasicInfoDiv--ImageDiv{
        max-width:50% !important;
    }
}


.BasicInfoDiv--ImageDiv{
    max-width:90%;
    max-height: 100%;
}
.BasicInfoDiv--ImageDiv img{
    max-width:100%;
    max-height: 100%;
}

.BasicInfoDiv--bottomInfo{
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
}

.BasicInfoDiv__bottomInfo--name{
    display: flex;
    flex-direction: column;
    grid-gap: 5px;
    gap: 5px;
}

.BasicInfoDiv__bottomInfo--contact{
    display: flex;
    flex-direction: column;
    grid-gap: 8px;
    gap: 8px;
}

.BasicInfoDiv__bottomInfo__contact--element{
    display: flex;
    grid-gap: 10px;
    gap: 10px;

    
}

.skillDiv{
    padding: 15px;

    display: flex;
    flex-direction: column;
    grid-gap:10px;
    gap:10px;
}

.skillDiv--skills{
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;

}



.projectElementsDiv{

    display: flex;
    flex-direction: row;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
    padding: 15px;
    
}


.projectElementsDiv--img{
    max-width: 100%;
    max-height: 100%;
}


.projectElementsDiv--info{

    height: 95%;
    
    width: 55%;

    display: flex;
    justify-content:space-between;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
}
.projectElementsDiv__info--hastag{
    display: flex;
    flex-wrap: wrap;
    grid-gap: 5px;
    gap: 5px;

}

.projectElementsDiv__info--mainDescription{
    display: flex;
    flex-direction: column;
    grid-gap: 12px;
    gap: 12px;
}

.projectElementsDiv__info--buttonsDiv{
    display: flex;
    grid-gap:10px;
    gap:10px
}

.projectElementsDiv__info--buttonsDiv a{
    transition: 0.35s ease-in-out;

    padding: 5px 20px;
    background-color: transparent;
    border: solid 1px #2F80ED;
    border-radius: 6px;
    font-size: 15px;
    color:#2F80ED;
}
.projectElementsDiv__info--buttonsDiv a:hover{
    background-color:#2F80ED;
    color:#FFFFFF;
}




.loadingContainer {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.lds-dual-ring {
    display: inline-block;
    width: 80px;
    height: 80px;
}
.lds-dual-ring:after {
    content: ' ';
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid #4f4f4f;
    border-color: #4f4f4f transparent #4f4f4f transparent;
    -webkit-animation: lds-dual-ring 1.2s linear infinite;
            animation: lds-dual-ring 1.2s linear infinite;
}
@-webkit-keyframes lds-dual-ring {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
@keyframes lds-dual-ring {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.projectsDiv {
    grid-column: span 2;
    grid-row: span 2;

    display: flex;
    flex-direction: column;
    grid-gap: 12px;
    gap: 12px;

    min-height: 1000px;
}

.projectsDiv--elements {
    display: flex;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
}

.projectsDiv--titileDiv {
    padding: 15px;
}

.hobbiesElement{
    display: flex;
    flex-direction: column;
    grid-gap: 15px;
    gap: 15px;
}
.hobbiesElement--infoDiv{
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
}

.hobbiesElement-imgDiv{
    height: 100%;
}   
.hobbiesElement-imgDiv img{
    max-width: 100%;
    max-height: 100%;
}
.hobbiesDiv{
    padding: 15px;
    display: flex;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
    grid-column-start: 1;
    grid-row-start: 3;
}
@media only screen and (max-width: 720px) {
    .hobbiesDiv{
        grid-column: span 2;
        grid-row-start: 7;
    }
}

body img{
    border-radius: 10px;
}

footer{
    text-align: center;
}

.accessibilityDiv{
    margin-top: 1vh;
    /* border: solid black; */
    display: flex;
    justify-content: center;
    grid-gap: 20px;
    gap: 20px;
}

.accessibilityDiv button{
    cursor: pointer;
    background-color: transparent;
    border: solid 1px ;
    padding: 5px;
    border-radius: 6px;

}

.container{
    margin-top: 5vh;
    display: flex;
    flex-direction: column;
    grid-gap: 3vh;
    gap: 3vh;
}

.elementStyle{
    border-radius: 12px;

}

.gridContainer{
    margin-left: auto;
    margin-right: auto;

    width: 95%;
    max-width: 1000px;


    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.2rem;
}

@media only screen and (max-width: 720px) {
    .gridContainer{
        grid-template-columns: repeat(2, 1fr);
    }
}


